/* ---------- Theme tokens ---------- */
:root {
  --bg: #0b0f17;   /* پس‌زمینه تیرهِ عمیق و شیک */
  --fg: #e5e7eb;   /* متن روشن */
  --muted: #94a3b8;/* متن فرعی */
}

:root.light {
  --bg: #f8fafc;   /* پس‌زمینه روشن */
  --fg: #0f172a;   /* متن تیره */
  --muted: #475569;/* متن فرعی */
}

/* ---------- Base ---------- */
html, body {
  height: 100%;
}

body {
  background: var(--bg);      /* پس‌زمینه‌ی صاف و تمیز */
  color: var(--fg);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* وینیت خیلی خیلی لطیف بدون خطوط؛ فقط برای عمق */
body::before {
  content: "";
  position: fixed;
  inset: 0;
  pointer-events: none;
  z-index: -1;
  /* هاله‌ی ملایم، بدون banding و خط—خیلی کم‌رنگ نگه داشتیم */
  background:
    radial-gradient(60% 35% at 50% 0%,
      rgba(255,255,255,0.035),
      transparent 60%);
}
.light body::before {
  background:
    radial-gradient(60% 35% at 50% 0%,
      rgba(0,0,0,0.035),
      transparent 60%);
}

/* ---------- Utilities ---------- */

/* متن گرادینتی (اگه خواستی نگه‌دار) */
.gradient-text{
  background: linear-gradient(90deg, #60a5fa, #22c55e, #14b8a6);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
}

/* شیشه‌ای کاملاً تمیز بدون خطوطِ اذیت‌کننده */
.glass{
  background: rgba(255,255,255,0.06);
  backdrop-filter: blur(24px) saturate(150%);
  -webkit-backdrop-filter: blur(24px) saturate(150%);
  border: 1px solid rgba(255,255,255,0.07); /* خیلی لطیف */
}
.light .glass{
  background: rgba(0,0,0,0.04);
  backdrop-filter: blur(20px) saturate(130%);
  -webkit-backdrop-filter: blur(20px) saturate(130%);
  border-color: rgba(0,0,0,0.06);
}

/* دکمه و کارت بدون سایه‌ی رنگی؛ حرفه‌ای و خنثی */
.button-3d{
  box-shadow:
    0 8px 24px rgba(0,0,0,0.35),
    inset 0 0 0 1px rgba(255,255,255,0.08);
}
.card-3d{
  box-shadow:
    0 12px 36px rgba(0,0,0,0.30),
    inset 0 0 0 1px rgba(255,255,255,0.06);
}

/* ---------- Scrollbar ---------- */
::-webkit-scrollbar{ width: 10px; height: 10px; }
::-webkit-scrollbar-thumb{ background: rgba(148,163,184,0.35); border-radius: 999px; }
.light ::-webkit-scrollbar-thumb{ background: rgba(100,116,139,0.35); }

/* ---------- Reduced motion ---------- */
@media (prefers-reduced-motion: reduce){
  *{ animation: none !important; transition: none !important; }
}
/* مخفی کردن کرسر سیستم وقتی فالوئر فعاله */
.custom-cursor, .custom-cursor * { cursor: none !important; }

/* حالت پیش‌فرض (دارک): زرد پرنور، blend روی screen */
.cursor-dot {
  background: #fde047; /* زرد */
  filter: drop-shadow(0 0 10px rgba(253,224,71,0.9));
  transition: background .25s ease, filter .25s ease;
}
.cursor-ring {
  box-shadow: 0 0 0 3px #fde047, 0 0 28px rgba(253,224,71,0.75);
  background: transparent;
  transition: box-shadow .25s ease, background .25s ease;
}
.cursor-ripple {
  border: 3px solid #fde047;
  box-shadow: 0 0 28px rgba(253,224,71,0.85);
  pointer-events: none;
}

/* تم روشن: سبز پرکنتراست + blend عادی + هاله تیرهٔ خیلی لطیف برای خوانایی */
.light .cursor-layer { mix-blend-mode: normal; }

.light .cursor-dot {
  background: #16a34a; /* green-600 */
  /* هاله‌ی سبز + سایه‌ی خیلی کمرنگ تیره برای کنتراست */
  filter:
    drop-shadow(0 0 10px rgba(22,163,74,0.9))
    drop-shadow(0 0 1px rgba(0,0,0,0.25));
}

.light .cursor-ring {
  /* پررنگ‌تر و کمی پرشده تا روی سفید گم نشه */
  box-shadow:
    0 0 0 3px #16a34a,
    0 0 30px rgba(22,163,74,0.9),
    0 0 1px rgba(0,0,0,0.25);
  background: rgba(22,163,74,0.08); /* پر داخلی لطیف */
}

.light .cursor-ripple {
  border: 3px solid #16a34a;
  box-shadow:
    0 0 30px rgba(22,163,74,0.9),
    0 0 2px rgba(0,0,0,0.25);
}

